nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    right: 10%;
    transform: translateX(-50%);
    top: .1rem;
    right: .1rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

}

nav a {
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    color: white;
    font-size: .9rem;
}

nav a:hover {
    background: #57737A;
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}